
p,
h1,
h2,
h3,
h4 {
    margin: 0;
}

ul {
    margin: 0;
    padding: 0;
}



.partners-container {
    display: flex;
    width: 1190px;
    height: 350px;
    margin-top: -70px;
    padding: 50px 25px;
    background: #FFFFFF;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.05);
    border-radius: 0px;
    align-items: baseline;
}

.partners-box-1 {

    width: 403px;
    height: 250px;
    padding-top: 56px;
    padding-bottom: 7px;
    padding-left: 15px;
    padding-right: 53px;
    border-right: 2px solid #E8E8E8;
}

.partners-title {
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 1.46;



    color: #000000;

}

.pre-title {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.88;



    color: #737373;
}

.partners-box-2 {
    display: flex;
    align-items: center;
    width: 710px;
    height: 250px;
    padding-top: 52px;
    
    padding-right: 13px;
    padding-left: 70px;
    margin-left: 11px;
}



.title-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 50px;

}


.list-item {
    width: 164px;
    font-weight: 400;
    font-size: 11px;
    line-height: 2.73;

    color: #737373;
}

.list {
    list-style: none;
}



